body {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: "Gotham", "Kazimir", sans-serif;
  font-size: 16px;
  line-height: 1;
  min-height: 100vh;
}
.wrapper {
  flex: 1 1 auto;
  position: relative;
  padding-top: 84px;
  max-width: 100%;
  width: 100%;
  overflow: hidden;
}
.container {
  position: relative;
  max-width: 1180px;
  width: 100%;
  margin: 0 auto;
  @include response(1200px) {
    padding-left: 25px;
    padding-right: 25px;
  }
}
a,svg {
  transition: .3s ease-in-out;
}
* {
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
