.main-products {
  position: relative;
  &__row {
    position: relative;
    background: #f8f8f8;
    margin-bottom: 67px;
    &:nth-child(even) {
      background: #fff;
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
      .main-products__box {
        flex-direction: row-reverse;
      }
      .main-products__info {
        margin-right: 0;
        margin-left: 70px;
      }
      .main-products__decor {
        left: auto;
        right: 0;
      }
      .main-products__row-decor {
        right: auto;
        left: calc(100% + 10px);
      }
    }
  }
  &__row-decor {
    position: absolute;
    right: calc(100% + 10px);
    bottom: 0;
  }
  &__box {
    position: relative;
    padding-top: 75px;
    padding-bottom: 75px;
    display: flex;
  }
  &__info {
    flex: 1 1 auto;
    margin-right: 70px;
    padding-top: 30px;
  }
  &__title-box {
    display: flex;
    align-items: center;
    margin-bottom: 25px;
    position: relative;
    z-index: 1;
  }
  &__title-img {
    flex: 0 0 75px;
    max-width: 75px;
    width: 100%;
    display: flex;
    margin-right: 30px;
  }
  &__title {
    font-weight: bold;
    font-size: 40px;
    line-height: 50px;
    color: #333;
  }
  &__text {
    position: relative;
    padding-left: 105px;
    z-index: 1;
  }
  &__decor {
    position: absolute;
    left: 0;
    bottom: 0;
  }
  &__list-box {
    flex: 0 0 700px;
    max-width: 700px;
    width: 100%;
    position: relative;
  }
  &__list {
    margin-left: -20px;
    display: flex;
  }
}
