.footer {
  position: relative;
  flex: 0 0 auto;
  max-width: 100%;
  width: 100%;
  overflow: hidden;
  &__container {
    padding-top: 48px;
    padding-bottom: 36px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    position: relative;
  }
  &__logo-box {
    flex: 0 0 250px;
    max-width: 250px;
    width: 100%;
    position: relative;
  }
  &__logo {
    position: relative;
    max-width: 135px;
    width: 135px;
    height: 34px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 25px;
    img {
      max-height: 100%;
    }
  }
  &__logo-link {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &__logo-descriptions {
    font-size: 12px;
    line-height: 19px;
    color: #808080;
  }
  &__nav {
    position: relative;
    display: flex;
    flex: 0 0 auto;
  }
  &__nav-cell {
    flex: 0 0 190px;
    max-width: 190px;
    width: 100%;
    margin-right: 120px;
    position: relative;
    &:last-child {
      margin-right: 0;
    }
    ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }
    li {
      margin-bottom: 12px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      display: block;
      text-decoration: none;
      font-size: 15px;
      line-height: 160%;
      font-weight: 500;
      color: #333;
      @include desktop-hover() {
        color: $purple;
      }
    }
  }
  &__nav-title {
    position: relative;
    margin-bottom: 19px;
    font-size: 15px;
    line-height: 160%;
    color: #808080;
  }
  &__subscribe {
    position: relative;
    flex: 0 0 255px;
    max-width: 255px;
    width: 100px;
  }
  &__subscribe-title {
    font-size: 15px;
    line-height: 160%;
    color: #808080;
    margin-bottom: 15px;
  }
  &__subscribe-form {
    position: relative;
  }
  &__subscribe-input {
    width: 100%;
    border: 1px solid #d7d7d7;
    border-radius: 50px;
    transition: .3s ease-in-out;
    height: 54px;
    padding: 0 60px 0 24px;
    font-size: 15px;
    color: #333;
    font-weight: 500;
    outline: none;
    &::placeholder {
      color: #808080;
      font-weight: 400;
    }
    &:focus {
      border-color: $purple;
    }
  }
  &__subscribe-btn {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0;
    background: transparent;
    cursor: pointer;
    padding: 0;
    @include desktop-hover() {
      svg {
        fill: $purple;
      }
    }
    svg {
      fill: #d4a9ed;
    }
  }
}
