.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #fff;
  z-index: 100;
  box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
  padding-top: 5px;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 5px;
    background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
  }
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    position: relative;
    flex: 0 0 135px;
    max-width: 135px;
    width: 135px;
    height: 34px;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      max-height: 100%;
    }
  }
  &__logo-link {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &__middle {
    flex: 0 0 auto;
    position: relative;
    display: flex;
    align-items: center;
  }
  &__catalog-btn {
    flex: 0 0 auto;
    position: relative;
    display: flex;
    align-items: center;
    text-decoration: none;
    margin-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    @include desktop-hover() {
      .header__catalog-btn-icon {
        transform: scale(0.9);
      }
    }
  }
  &__catalog-btn-text {
    font-size: 13px;
    font-weight: 500;
    color: $purple;
    padding-top: 7px;
  }
  &__catalog-btn-icon {
    flex: 0 0 auto;
    margin-right: 12px;
    fill: $purple;
  }
  &__nav {
    position: relative;
    > ul {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      align-items: center;
    }
    li {
      position: relative;
      @include desktop-hover() {
        ul {
          opacity: 1;
          transform: translateY(0);
          visibility: visible;
        }
      }
    }
    a {
      display: flex;
      align-items: center;
      padding: 33px 20px 26px;
      font-size: 13px;
      line-height: 160%;
      color: #333;
      text-decoration: none;
      position: relative;
      font-weight: 500;
      @include desktop-hover() {
        background: rgba($purple, 0.05);
      }
    }
  }
  &__controls {
    position: relative;
    flex: 0 0 auto;
    display: flex;
    align-items: center;
  }
  &__search {
    flex: 0 0 auto;
    position: relative;
  }
  &__search-btn {
    display: flex;
    cursor: pointer;
    padding: 15px;
    @include desktop-hover() {
      svg {
        fill: $purple;
      }
    }
    svg {
      fill: #a6b6b6;
    }
  }
  &__search-form {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s ease-in-out;
    .header__search.active & {
      opacity: 1;
      visibility: visible;
    }
  }
  &__search-input {
    width: 200px;
    border: 0;
    border-bottom: 1px solid #333;
    outline: none;
    padding-left: 15px;
    padding-right: 15px;
  }
  &__search-button {
    display: flex;
    background: #fff;
    border: 0;
    cursor: pointer;
    padding: 15px;
    @include desktop-hover() {
      svg {
        fill: $purple;
      }
    }
    svg {
      fill: #a6b6b6;
    }
  }
  &__basket {
    position: relative;
    flex: 0 0 auto;
    display: flex;
    padding: 15px;
    cursor: pointer;
    @include desktop-hover() {
      svg {
        fill: $purple;
      }
    }
    svg {
      fill: #a6b6b6;
    }
  }
  &__basket-count {
    position: absolute;
    top: 0;
    right: 0;
    width: 18px;
    height: 18px;
    background: #f15a89;
    color: #fff;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    border-radius: 50%;
  }
  &__popup {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    height: calc(100vh - 84px);
    background: rgba(#000, 0.5);
    opacity: 0;
    visibility: hidden;
    transition: 0.3s ease-in-out;
    &.active {
      opacity: 1;
      visibility: visible;
    }
  }
  &__popup-container {
    position: relative;
    display: flex;
    justify-content: flex-end;
    padding-top: 20px;
  }
  &__popup-box {
    position: relative;
    flex: 0 0 500px;
    max-width: 500px;
    width: 100%;
    background: #fff;
    border-radius: 10px;
    overflow: hidden;
  }
  &__popup-head {
    background: #f8f8f8;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 14px 18px 14px 40px;
  }
  &__popup-head-left {
    display: flex;
    align-items: center;
  }
  &__popup-head-icon {
    flex: 0 0 23px;
    max-width: 23px;
    width: 23px;
    height: 23px;
    border-radius: 50%;
    background: #f15a89;
    position: relative;
    margin-right: 18px;
    &::before {
      content: "";
      position: absolute;
      width: 11px;
      height: 3px;
      background: #fff;
      left: 6px;
      top: 10px;
    }
    &::after {
      content: "";
      position: absolute;
      height: 11px;
      width: 3px;
      background: #fff;
      top: 6px;
      left: 10px;
    }
  }
  &__popup-head-text {
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: #333;
  }
  &__popup-close {
    display: flex;
    flex: 0 0 auto;
    cursor: pointer;
    padding: 15px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      transform: rotate(90deg);
    }
  }
  &__popup-holder {
    position: relative;
    padding: 30px;
  }
  &__popup-controls {
    position: relative;
    display: flex;
    margin-left: -20px;
  }
  &__popup-btn {
    flex: 0 0 calc(50% - 20px);
    margin-left: 20px;
  }
}

.menu-item-has-children {
  @include desktop-hover() {
    > a {
      &::after {
        transform: rotate(45deg) scale(-1);
        margin-top: 0;
      }
    }
  }
  > a {
    &::after {
      content: "";
      display: inline-block;
      flex: 0 0 auto;
      margin-left: 7px;
      width: 5px;
      height: 5px;
      border-bottom: 1px solid #333;
      border-right: 1px solid #333;
      transform: rotate(45deg);
      margin-top: -2px;
      transition: transform .3s ease-in-out;
    }
  }
}
.sub-menu {
  list-style: none;
  padding: 0;
  margin: 0;
  position: absolute;
  top: 100%;
  left: 0;
  width: 280px;
  background: #fff;
  box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
  opacity: 0;
  visibility: hidden;
  transform: translateY(10px);
  transition: 0.3s ease-in-out;
  a {
    padding-top: 12px;
    padding-bottom: 12px;
  }
}
