.product {
  position: relative;
  background: #fff;
  border: 1px solid #e9e9e9;
  border-radius: 5px;
  outline: none;
  padding: 50px 15px 23px;
  transition: 0.3s ease-in-out;
  flex: 0 0 calc(25% - 20px);
  max-width: calc(25% - 20px);
  width: 100%;
  margin-left: 20px;
  .slick-slide & {
    max-width: 100%;
    margin-left: 0;
  }
  .main-products__list & {
    flex: 0 0 calc(33.333% - 20px);
    max-width: calc(33.333% - 20px);
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  @include desktop-hover() {
    box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.05);
    border-color: #fff;
    .product__btn {
      color: #fff;
      svg {
        fill: #fff;
      }
      span {
        display: block;
      }
      &::before {
        opacity: 1;
        transform: scaleX(1);
      }
    }
  }
  &__label {
    position: absolute;
    top: 15px;
    left: 15px;
    text-transform: uppercase;
    margin-bottom: 15px;
    display: inline-block;
    color: #fff;
    text-align: center;
    font-weight: bold;
    font-size: 12px;
    padding: 5px 12px;
    border-radius: 15px;
    background: #f15a89;
    &--sale {
      background: #f15a89;
    }
    &--new {
      background: #333;
    }
  }
  &__img {
    display: block;
    margin-bottom: 15px;
    border-radius: 10px;
    overflow: hidden;
    outline: none;
    @include desktop-hover() {
      img {
        transform: scale(1.02);
      }
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.3s ease-in-out;
    }
  }
  &__title {
    display: block;
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: #333;
    margin-bottom: 10px;
    text-decoration: none;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: 500;
    .main-products__list & {
      max-height: 50px;
      white-space: normal;
    }
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
  &__price-box {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__price-new {
    font-size: 20px;
    line-height: 160%;
    color: #f15a89;
    flex: 0 0 auto;
    margin-right: 7px;
    font-weight: 500;
  }
  &__price {
    font-size: 20px;
    line-height: 160%;
    color: #333;
    flex: 0 0 auto;
    font-weight: 500;
  }
  &__price-old {
    font-size: 16px;
    line-height: 160%;
    text-decoration-line: line-through;
    color: #a6b6b6;
    flex: 0 0 auto;
  }
  &__btn-box {
    position: relative;
    text-align: center;
    padding-left: 17px;
    padding-right: 17px;
    margin-top: 15px;
  }
  &__btn {
    width: 100%;
  }
}
