.btn-1 {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  padding: 12px 30px;
  height: 52px;
  border: 0;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  font-weight: bold;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
  transform: perspective(1px);
  @include desktop-hover() {
    color: $purple;
    svg {
      fill: #000;
    }
    &::before {
      opacity: 1;
    }
  }
  &::before {
    content: "";
    position: absolute;
    top: 2px;
    left: 2px;
    right: 2px;
    bottom: 2px;
    border-radius: 5px;
    background: #fff;
    z-index: -1;
    transition: 0.3s ease-in-out;
    opacity: 0;
  }
  svg {
    flex: 0 0 auto;
    margin-right: 10px;
    fill: #fff;
  }
}

