.btn-4 {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  padding: 12px 30px;
  height: 52px;
  border: 0;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  font-weight: bold;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  transform: perspective(1px);
  @include desktop-hover() {
    color: #fff;
    svg {
      fill: #fff;
    }
    span {
      display: block;
    }
    &::before {
      opacity: 1;
      transform: scaleX(1);
    }
  }
  span {
    display: none;
    margin-left: 10px;
  }
  &::before {
    content: "";
    background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
    position: absolute;
    top: 2px;
    left: 2px;
    right: 2px;
    bottom: 2px;
    border-radius: 5px;
    z-index: -1;
    opacity: 0;
    transform: scaleX(0);
  }
  svg {
    flex: 0 0 auto;
    fill: #a6b6b6;
    transition: none;
  }
}

.btn-5 {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  border: 0;
  outline: none;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  font-size: 16px;
  line-height: 26px;
  font-weight: 700;
  color: #fff;
  @include desktop-hover() {
    color: #000;
    svg {
      fill: #000;
    }
  }
  svg {
    flex: 0 0 auto;
    fill: #fff;
    margin-left: 15px;
  }
  &--purple {
    color: $purple;
    svg {
      fill: $purple;
    }
  }
}
