.btn-top {
  position: relative;
  display: flex;
  cursor: pointer;
  @include desktop-hover() {
    .btn-top__arrow {
      transform: translateY(-4px);
    }
  }
  &__arrow {
    position: absolute;
    left: 50%;
    margin-left: -5px;
    bottom: 40%;
    stroke: #fff;
    stroke-width: 2;
    fill: none;
  }
  &__bg {
    width: 100%;
    height: auto;
  }
}