.articles {
  position: relative;
  padding-top: 75px;
  padding-bottom: 25px;
  background: #f8f8f8;
  &__title {
    margin-bottom: 40px;
  }
  &__list {
    display: flex;
    margin-left: -20px;
  }
}

.article {
  position: relative;
  width: 100%;
  flex: 0 0 calc(33.333% - 66px);
  max-width: calc(33.333% - 66px);
  margin-bottom: 50px;
  margin-left: 66px;
  .articles__list & {
    flex: 0 0 calc(25% - 20px);
    max-width: calc(25% - 20px);
    margin-left: 20px;
  }
  &__img {
    display: flex;
    position: relative;
    height: 210px;
    overflow: hidden;
    background: #f8f8f8;
    border-radius: 10px;
    margin-bottom: 30px;
    @include desktop-hover() {
      img {
        transform: scale(1.02);
      }
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.3s ease-in-out;
    }
  }
  &__date {
    position: absolute;
    bottom: 10px;
    left: 10px;
    background: #fff;
    padding: 12px 16px;
    font-weight: bold;
    font-size: 12px;
    text-align: center;
    color: #333;
    border-radius: 10px;
  }
  &__title {
    display: block;
    margin-bottom: 30px;
    font-size: 20px;
    line-height: 32px;
    max-height: 64px;
    overflow: hidden;
    color: #333;
    text-decoration: none;
    font-weight: 500;
    @include desktop-hover() {
      color: $purple;
    }
  }
  &__descriptions {
    position: relative;
    margin-bottom: 30px;
    font-size: 16px;
    line-height: 26px;
    color: #333333;
  }
}
