.text-content {
  position: relative;
  font-family: "Gotham", sans-serif;
  font-size: 16px;
  line-height: 26px;
  font-weight: 400;
  color: #333;
  p {
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
