  iframe {
    border: 0;
    max-width: 100%;
    vertical-align: top;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    @include response(767px) {
      width: 100%;
      height: 300px;
    }
  }
  h1 {
    font-size: 36px;
    line-height: 120%;
    font-weight: 500;
    margin: 10px 0 20px;
    @include response(767px) {
      font-size: 26px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h2 {
    font-size: 26px;
    line-height: 140%;
    margin: 10px 0 20px;
    font-weight: 500;
    @include response(767px) {
      font-size: 22px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h3 {
    font-size: 22px;
    line-height: 140%;
    font-weight: 500;
    margin: 10px 0 20px;
    @include response(767px) {
      font-size: 20px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h4 {
    font-size: 20px;
    line-height: 140%;
    font-weight: 500;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
  }
  img {
    display: inline-block;
    vertical-align: top;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
  }
  a {
    color: $purple;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
}
