.first-screen {
  position: relative;
  box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
  overflow: hidden;
  &__img {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  &__box {
    padding-top: 106px;
    padding-bottom: 53px;
    position: relative;
    text-align: center;
  }
  &__label {
    background: #f15a89;
    border-radius: 5px;
    position: relative;
    font-weight: 700;
    font-size: 64px;
    line-height: 120%;
    color: #fff;
    margin-bottom: 26px;
    display: inline-block;
    vertical-align: top;
    padding: 16px 30px;
  }
  &__title {
    font-size: 64px;
    line-height: 120%;
    color: #fff;
    margin-bottom: 39px;
  }
  &__descriptions {
    font-size: 14px;
    line-height: 180%;
    color: #f7edff;
    position: relative;
    margin-bottom: 15px;
  }
}
