.basket-list {
  margin-bottom: 30px;
}
.basket-item {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
  &__img {
    flex: 0 0 150px;
    max-width: 150px;
    width: 100%;
    height: 150px;
    margin-right: 30px;
    overflow: hidden;
    border-radius: 10px;
    @include desktop-hover() {
      img {
        transform: scale(1.05);
      }
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.3s ease-in-out;
    }
  }
  &__box {
    flex: 1 1 auto;
  }
  &__title {
    margin-bottom: 10px;
    font-size: 16px;
    line-height: 160%;
    color: #333;
    font-weight: 500;
    text-decoration: none;
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
  &__price {
    font-size: 20px;
    line-height: 160%;
    color: #333;
    font-weight: 500;
    margin-bottom: 10px;
  }
  &__descriptions {
    color: $purple;
    font-size: 14px;
    line-height: 180%;
  }
  &__remove {
    flex: 0 0 auto;
    position: relative;
    display: flex;
    cursor: pointer;
    padding: 10px;
    margin-right: -10px;
    @include desktop-hover() {
      svg {
        fill: #000;
      }
    }
    svg {
      fill: #a6b6b6;
    }
  }
}
