.featured-products {
  position: relative;
  padding-top: 75px;
  padding-bottom: 55px;
  &__title {
    margin-bottom: 50px;
  }
  &__slider {
    position: relative;
    margin-left: -19px;
    margin-right: -19px;
    .slick-slide {
      margin-left: 10px;
      margin-right: 10px;
    }
    .slick-list {
      padding-top: 20px;
      padding-bottom: 40px;
      padding: 20px 9px 40px;
    }
    .slick-arrow {
      position: absolute;
      cursor: pointer;
      z-index: 1;
      display: flex;
      top: 50%;
      margin-top: -41px;
      @include desktop-hover() {
        svg {
          fill: #333;
        }
      }
      svg {
        fill: #a6b6b6;
      }
    }
    .slick-prev {
      left: -70px;
    }
    .slick-next {
      right: -70px;
      svg {
        transform: scaleX(-1);
      }
    }
  }
  &__item {
    position: relative;
  }
}
