.resources {
  position: relative;
  padding-bottom: 75px;
  &__title {
    margin-bottom: 34px;
  }
  &__list {
    display: flex;
    margin-left: -78px;
  }
  &__item {
    flex: 0 0 calc(50% - 78px);
    max-width: calc(50% - 78px);
    width: 100%;
    margin-left: 78px;
    border-radius: 10px;
    background: $purple;
    padding: 40px 26px 40px 40px;
    display: flex;
    align-items: center;
    position: relative;
    &:nth-child(even) {
      background: #F15A89;
    }
  }
  &__item-decor {
    position: absolute;
    top: 0;
    right: 0;
  }
  &__item-img {
    flex: 0 0 auto;
    margin-right: 60px;
    border-radius: 10px;
    overflow: hidden;
    margin-left: -80px;
  }
  &__item-box {
    flex: 1 1 auto;
  }
  &__item-title {
    margin-bottom: 20px;
    position: relative;
    font-weight: bold;
    font-size: 28px;
    line-height: 45px;
    color: #fff;
  }
  &__item-text {
    position: relative;
    color: #fff;
    font-size: 16px;
    margin-bottom: 21px;
    line-height: 26px;
    padding-right: 20px;
  }
}
